import Notiflix from 'notiflix';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import './css/styles.css';
import SlimSelect from 'slim-select';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import '../node_modules/slim-select/dist/slimselect.css';
import {
  fetchBreeds,
  renderBreeds,
  fetchCatByBreed,
  renderCat,
} from './js/cat-api';

//   ,    .
const selectContainerEl = document.querySelector('.select-container');
//       .
const selectEl = document.querySelector('#selectElement');
//   ,      .
const catInfoEl = document.querySelector('.cat-info');

//  
function showError(error) {
  console.log(error);
  selectContainerEl.classList.add('js-visually-hidden');//       
  Notify.failure(`Oops! Something went wrong! Try reloading the page!`, { //    
    position: 'center-center',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    clickToClose: true,
    timeout: 10000,
    width: '260px',
  });
}

//    
fetchBreeds()
  .then(data => {
    //  HTML-     selectEl
    selectEl.insertAdjacentHTML('beforeend', renderBreeds(data));
    //   "js-visually-hidden"   selectContainerEl,     
    selectContainerEl.classList.remove('js-visually-hidden');
    //    SlimSelect   
    new SlimSelect({
      select: '#selectElement',
      settings: {
        allowDeselect: true,
        placeholderText: `Select cat's breed ...`,
      },
      events: {
        //    
        afterChange: newVal => {
          //    catInfoEl
          catInfoEl.innerHTML = '';
          //    
          if (newVal.length !== 0) {
            //       
            fetchCatByBreed(newVal[0].value)
              .then(data => {
                //       catInfoEl
                catInfoEl.innerHTML = renderCat(data);
              })
              .catch(err => {
                //       
                showError(err);
              });
          }
        },
      },
    });
  })
  .catch(err => {
    //       
    showError(err);
  });
